/*  <general>  */
html {
  font-size: 62.5%;
  cursor: default;
}
-webkit-text-size-adjust 100%,
-ms-text-size-adjust 100% {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #3f4040;
  font: normal 1.6rem/1.5 'Rubik';
  background-color: #fff;
}
:-moz-placeholder {
  opacity: 1;
  color: currentColor;
}
:-ms-input-placeholder {
  opacity: 1;
  color: currentColor;
}
a {
  color: #0098dc;
  font: inherit;
  text-decoration: none;
  transition: all 0.2s;
}
a:hover {
  text-decoration: underline;
}
img {
  max-width: 100%;
}
.main {
  flex: 1;
}
/*  </general>  */
/*  <header>  */
.header {
  flex-shrink: 0;
  text-align: center;
}
@media screen and (min-width: 480px) {
  .header__main {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
@media screen and (min-width: 768px) {
  .header__main {
    display: block;
    position: relative;
    padding-top: 170px;
  }
}
@media screen and (min-width: 992px) {
  .header__main {
    height: 196px;
    padding-top: 0;
  }
}
.header__target {
  font-size: 1.8rem;
  text-transform: uppercase;
  border-bottom: 6px solid #dfdddb;
  padding: 15px 0 10px;
}
@media screen and (min-width: 480px) {
  .header__target {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}
@media screen and (min-width: 768px) {
  .header__target {
    position: absolute;
    top: 20px;
    right: 0;
    left: 285px;
    width: auto;
    border-bottom: 0;
    padding: 0;
  }
}
@media screen and (min-width: 992px) {
  .header__target {
    display: block;
    right: auto;
    left: 260px;
  }
}
@media screen and (min-width: 1200px) {
  .header__target {
    left: 300px;
  }
}
.header__target a {
  color: currentColor;
  text-decoration: underline;
}
.header__target a:hover {
  text-decoration: none;
}
.header__target a:last-child {
  display: block;
  margin-top: 10px;
}
@media screen and (min-width: 480px) {
  .header__target a:last-child {
    margin-top: 0;
  }
}
@media screen and (min-width: 992px) {
  .header__target a:last-child {
    display: inline;
    margin-left: 30px;
  }
}
.header__logo {
  margin: 20px 0;
}
@media screen and (min-width: 480px) {
  .header__logo {
    width: 45%;
    text-align: left;
    margin-bottom: 0;
  }
}
@media screen and (min-width: 768px) {
  .header__logo {
    position: absolute;
    top: 26px;
    left: 0;
    max-width: 260px;
    margin: 0;
  }
  .header__logo img {
    max-width: 200px;
  }
}
@media screen and (min-width: 992px) {
  .header__logo {
    max-width: 222px;
  }
  .header__logo img {
    max-width: 100%;
  }
}
.header__logo-slogan {
  font-size: 1.8rem;
  margin-top: 5px;
}
@media screen and (min-width: 992px) {
  .header__logo-slogan {
    width: 180px;
  }
}
.header__contacts {
  font-size: 1.8rem;
  border-bottom: 6px solid #dfdddb;
  padding-bottom: 15px;
}
@media screen and (min-width: 480px) {
  .header__contacts {
    width: 45%;
    border-bottom: 0;
    margin-top: 20px;
  }
}
@media screen and (min-width: 768px) {
  .header__contacts {
    position: absolute;
    top: 60px;
    right: 0;
    left: 285px;
    width: auto;
    text-align: left;
    padding-right: 155px;
    padding-bottom: 0;
    margin-top: 0;
  }
}
@media screen and (min-width: 992px) {
  .header__contacts {
    top: 20px;
    right: 0;
    left: auto;
    width: 250px;
    text-align: center;
    padding-right: 0;
  }
}
@media screen and (min-width: 1200px) {
  .header__contacts {
    display: flex;
    flex-direction: column;
    top: 30px;
    width: 260px;
    line-height: 1;
    text-align: right;
  }
  .header__contacts-title {
    margin-bottom: 15px;
  }
}
.header__contacts-phone {
  color: #3f4040;
  font-weight: bold;
  font-size: 2rem;
  text-decoration: none;
}
@media screen and (min-width: 768px) {
  .header__contacts-phone {
    font-size: 2.4rem;
  }
}
@media screen and (min-width: 1200px) {
  .header__contacts-phone {
    margin: 10px 0;
  }
}
.header__contacts-workhours {
  font-weight: 300;
}
@media screen and (min-width: 1200px) {
  .header__contacts-workhours {
    margin: 10px 0;
  }
}
.header__contacts-links {
  font-weight: 500;
  margin-top: 5px;
}
@media screen and (min-width: 768px) {
  .header__contacts-links {
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: 0;
    transform: translateY(-50%);
  }
}
@media screen and (min-width: 992px) {
  .header__contacts-links {
    position: static;
    margin-top: 15px;
  }
}
.header__contacts-links a {
  color: #38ae25;
  text-decoration: underline;
}
@media screen and (min-width: 992px) {
  .header__contacts-links a {
    color: currentColor;
    margin-right: 10px;
  }
}
.header__contacts-links a:last-child {
  display: none;
}
@media screen and (min-width: 992px) {
  .header__contacts-links a:last-child {
    display: inline;
    margin-right: 0;
  }
}
.header__contacts-links a:hover {
  text-decoration: none;
}
@media screen and (min-width: 992px) {
  .header__contacts-links a:hover {
    color: #38ae25;
  }
}
.header__action {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0 10px;
}
@media screen and (min-width: 480px) {
  .header__action {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    text-align: left;
    border-top: 6px solid #dfdddb;
    padding-top: 20px;
    margin-top: 0;
  }
}
@media screen and (min-width: 768px) {
  .header__action {
    text-align: center;
    border-top: 0;
    padding-top: 0;
    margin-bottom: 0;
  }
}
@media screen and (min-width: 992px) {
  .header__action {
    display: flex;
    justify-content: space-between;
    position: absolute;
    top: 67px;
    left: 260px;
    width: 440px;
  }
}
@media screen and (min-width: 1200px) {
  .header__action {
    top: 76px;
    left: 300px;
    width: 482px;
  }
}
.header__action-item {
  position: relative;
  color: #3f4040;
  font-size: 1.8rem;
  padding-top: 44px;
  padding-bottom: 5px;
  margin-bottom: 20px;
}
@media screen and (min-width: 480px) {
  .header__action-item {
    width: 30%;
  }
}
@media screen and (min-width: 768px) {
  .header__action-item {
    width: auto;
    white-space: nowrap;
  }
}
@media screen and (min-width: 992px) {
  .header__action-item {
    text-align: left;
    white-space: pre-wrap;
    margin-bottom: 0;
  }
  .header__action-item:first-child {
    width: 156px;
  }
  .header__action-item:nth-child(2) {
    width: 84px;
  }
  .header__action-item:last-child {
    width: 136px;
  }
}
.header__action-item::after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #ed8223;
  content: '';
  transition: all 0.2s;
}
a:hover.header__action-item {
  text-decoration: none;
}
a:hover.header__action-item::after {
  width: 100%;
}
.header__action-item svg {
  position: absolute;
  top: 0;
  left: 50%;
  width: 35px;
  height: 35px;
  fill: #3f4040;
  transform: translateX(-50%);
  transition: all 0.2s;
}
@media screen and (min-width: 480px) {
  .header__action-item svg {
    left: 0;
    transform: none;
  }
}
@media screen and (min-width: 768px) {
  .header__action-item svg {
    left: 50%;
    transform: translateX(-50%);
  }
}
@media screen and (min-width: 992px) {
  .header__action-item svg {
    left: 0;
    transform: none;
  }
}
a:hover.header__action-item {
  color: #ed8223;
}
a:hover.header__action-item svg {
  fill: #ed8223;
}
.header__action-item:last-child {
  margin-bottom: 0;
}
/*  </header>  */
/*  <nav>  */
.nav {
  flex-shrink: 0;
  background-color: #38ae25;
}
.nav__main {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 15px;
}
@media screen and (min-width: 480px) {
  .nav__main {
    margin-left: -28px;
  }
}
@media screen and (min-width: 640px) {
  .nav__main {
    margin-left: -25px;
  }
}
@media screen and (min-width: 992px) {
  .nav__main {
    flex-wrap: nowrap;
    justify-content: space-between;
    margin-left: 0;
  }
}
.nav__item {
  width: 55%;
  color: #fff;
  font-size: 1.8rem;
  text-transform: uppercase;
  margin-top: 20px;
}
@media screen and (min-width: 480px) {
  .nav__item {
    width: auto;
    margin-top: 15px;
    margin-left: 28px;
  }
}
@media screen and (min-width: 640px) {
  .nav__item {
    margin-left: 25px;
  }
}
@media screen and (min-width: 992px) {
  .nav__item {
    margin-left: 0;
  }
}
.nav__item:nth-child(even) {
  width: 45%;
}
@media screen and (min-width: 480px) {
  .nav__item:nth-child(even) {
    width: auto;
  }
}
.nav__item--arrow {
  display: inline-flex;
  align-items: center;
}
.nav__item--arrow::after {
  width: 13px;
  height: 13px;
  border: solid #fff;
  border-width: 0 0 2px 2px;
  margin-left: 10px;
  transform: translateY(calc(-50% + 1px)) rotate(-45deg);
  content: '';
}
/*  </nav>  */
/*  <footer>  */
.footer {
  flex-shrink: 0;
  background-color: #242424;
}
.footer__main {
  color: #fff;
  text-align: center;
  margin: 40px 0 35px;
}
@media screen and (min-width: 992px) {
  .footer__main {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
  }
}
@media screen and (min-width: 480px) {
  .footer__logo {
    display: block;
    max-width: 80%;
    margin: auto;
  }
}
@media screen and (min-width: 992px) {
  .footer__logo {
    width: 330px;
    margin: 0;
  }
}
.footer__slogan {
  font-size: 1.8rem;
  margin: 15px 0 25px;
}
.footer__slogan span {
  display: block;
}
@media screen and (min-width: 992px) {
  .footer__slogan {
    position: absolute;
    top: 118px;
    left: 0;
    width: calc(20% - 10px);
    text-align: left;
  }
}
.footer__action {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0 10px;
}
@media screen and (min-width: 480px) {
  .footer__action {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    margin-right: 18px;
    margin-left: 18px;
    margin-bottom: 25px;
  }
}
@media screen and (min-width: 640px) {
  .footer__action {
    margin: 0 0 30px;
  }
}
@media screen and (min-width: 992px) {
  .footer__action {
    width: calc(60% - 5px);
  }
}
.footer__action-item {
  position: relative;
  color: #fff;
  font-size: 1.8rem;
  padding-top: 44px;
  padding-bottom: 5px;
  margin-bottom: 20px;
}
@media screen and (min-width: 480px) {
  .footer__action-item {
    text-align: left;
    margin-bottom: 0;
  }
  .footer__action-item:first-child {
    width: 152px;
  }
  .footer__action-item:nth-child(2) {
    width: 85px;
  }
  .footer__action-item:last-child {
    width: 135px;
  }
}
@media screen and (min-width: 640px) {
  .footer__action-item {
    text-align: center;
  }
  .footer__action-item:first-child,
  .footer__action-item:nth-child(2),
  .footer__action-item:last-child {
    width: auto;
  }
}
@media screen and (min-width: 768px) {
  .footer__action-item {
    width: auto;
    white-space: nowrap;
  }
}
@media screen and (min-width: 992px) {
  .footer__action-item {
    white-space: pre-wrap;
    text-align: left;
  }
  .footer__action-item:first-child,
  .footer__action-item:nth-child(2),
  .footer__action-item:last-child {
    width: calc(33% - 5px);
  }
}
.footer__action-item::after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #ed8223;
  content: '';
  transition: all 0.2s;
}
a:hover.footer__action-item {
  text-decoration: none;
}
a:hover.footer__action-item::after {
  width: 100%;
}
.footer__action-item svg {
  position: absolute;
  top: 0;
  left: 50%;
  width: 35px;
  height: 35px;
  fill: #fff;
  transform: translateX(-50%);
  transition: all 0.2s;
}
@media screen and (min-width: 480px) {
  .footer__action-item svg {
    left: 0;
    transform: none;
  }
}
@media screen and (min-width: 640px) {
  .footer__action-item svg {
    left: 50%;
    transform: translateX(-50%);
  }
}
@media screen and (min-width: 992px) {
  .footer__action-item svg {
    left: 0;
    transform: none;
  }
}
a:hover.footer__action-item {
  color: #ed8223;
}
a:hover.footer__action-item svg {
  fill: #ed8223;
}
.footer__action-item:last-child {
  margin-bottom: 0;
}
@media screen and (min-width: 640px) {
  .footer__cols {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    text-align: left;
  }
  .footer__col {
    width: 32%;
  }
  .footer__col:nth-child(1) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .footer__col:first-child,
